/* =====================================================
   Fractal UI styles
   ===================================================== */
/* Theme overrides */
a {
  color: purple;
}

a:hover {
  color: rgba(128, 0, 128, 0.5);
}

a:active {
  color: pink;
}

.Pen-previewLink svg {
  fill: pink;
}

.Header {
  background-color: pink;
  color: purple;
}

.Prose tr td:first-child {
  min-width: 12em;
  padding-right: 1em;
  white-space: nowrap;
}

.Prose tr td:last-child {
  width: 100%;
}
